<script>
	export default {
		data() {
			return {
				rotatShowStatus: false,
				timer: null,
				isLoading: true,
				configurationHeight: "640",
				paytableContentHeight: "577",
				ops: {
					vuescroll: {},
					scrollPanel: {
						scrollingX: false, //关闭横向滚动
					},
					rail: {
						keepShow: true,
					},
					bar: {
						hoverStyle: true,
						onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
						background: "hsla(0, 0%, 46%)", //滚动条颜色
						opacity: 0.7, //滚动条透明度
					},
				},
			}
		},
		mounted() {
			this.configurationHeight = window.configurationHeight
			this.paytableContentHeight = window.paytableContentHeight
		},
		methods: {
			open() {
				this.isLoading = true
				this.rotatShowStatus = true
				this.timer = setTimeout(() => {
					this.isLoading = false
				}, 1000)
				this.$nextTick(() => {
					this.$refs.scrollContainer.scrollTo({
						top: 0,
						behavior: 'smooth'
					});
				});
			},

			closeTable() {
				this.rotatShowStatus = false
				window.changeTrophiesIconShow(true);
			}
		}
	}
</script>

<style>
	body {
		font-family: PingFang SC, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif !important;
	}

	.rotating {
		position: absolute;
		top: 0;
		/* left: 58px; */
		height: 100%;
		width: 100%;
		z-index: 998;
		transition: all 0.2s;
		background-color: #041a2e;

	}

	.paytable {
		position: absolute;
		top: 0;
		z-index: 998;
		transition: all 0.2s;
		background-color: #041a2e;
		animation: shake 0.82s ease;
		animation-fill-mode: forwards;
	}


	@keyframes shake {
		from {
			transform: translateY(100%);
			display: none;
		}

		to {
			transform: translateY(0%);
			display: block;
		}
	}

	.paytable1 {
		position: absolute;
		top: 0;
		z-index: 998;
		transition: all 0.2s;
		background-color: #041a2e;
		animation: shake1 0.82s ease-out;
		animation-fill-mode: forwards;
	}


	@keyframes shake1 {
		from {
			transform: translateY(0%);
			display: block;
		}

		to {
			transform: translateY(100%);
			display: none;
		}
	}

	/* .moveL-enter-active,
.moveL-leave-active {
    transition: all 0.2s linear;
    transform: translate3d(0, 0%, 0);
}

.moveL-enter,
.moveL-leave {
    transform: translate3d(0, 100%, 0);
}

.moveL-leave-to {
    transform: translate3d(0, 100%, 0);
} */

	.paytable .rcs-custom-scroll,
	.paytable-land .rcs-custom-scroll,
	.rules .rcs-custom-scroll {
		min-height: 0;
		min-width: 0;
	}

	::-webkit-scrollbar {
		width: 0 !important;
	}

	::-webkit-scrollbar {
		width: 0 !important;
		height: 0;
	}

	.paytable .rcs-custom-scroll .rcs-outer-container,
	.paytable-land .rcs-custom-scroll .rcs-outer-container,
	.rules .rcs-custom-scroll .rcs-outer-container {
		overflow: hidden;
	}

	.paytable .rcs-custom-scroll .rcs-outer-container .rcs-positioning,
	.rules .rcs-custom-scroll .rcs-outer-container .rcs-positioning {
		position: unset;
	}

	.paytable .rcs-custom-scroll .rcs-custom-scrollbar,
	.paytable-land .rcs-custom-scroll .rcs-custom-scrollbar,
	.rules .rcs-custom-scroll .rcs-custom-scrollbar {
		box-sizing: border-box;
		height: 100%;
		opacity: 0;
		padding: 6px 0;
		pointer-events: none;
		position: absolute;
		right: 3px;
		transition: opacity .4s ease-out;
		width: 6px;
		will-change: opacity;
		z-index: 1;
	}

	.paytable .rcs-custom-scroll .rcs-inner-handle,
	.paytable-land .rcs-custom-scroll .rcs-inner-handle,
	.rules .rcs-custom-scroll .rcs-inner-handle {
		background-color: hsla(0, 0%, 46%, .7);
		border-radius: 3px;
		height: calc(100% - 12px);
		margin-top: 6px;
	}

	.paytable::-webkit-scrollbar {
		display: none;
