import Vue from 'vue'
import App from './App.vue'
import router from "./assets/js/router.js"
import service from '../src/assets/js/http.js'

Vue.prototype.$http = service

// 引入 vue-i18n
import VueI18n from 'vue-i18n'
import pt from '../src/assets/js/lang/pt.js'
import en from '../src/assets/js/lang/en.js'
Vue.use(VueI18n)

// 语言包
const messages = {
	en: en,
	pt: pt
}

// 读取 URL 参数 ?language=
const urlParams = new URLSearchParams(window.location.search)
const languageParam = urlParams.get('language')
const supportedLangs = ['en', 'pt']
const language = supportedLangs.includes(languageParam) ? languageParam : 'pt'

// 创建 i18n 实例
const i18n = new VueI18n({
	locale: language,
	fallbackLocale: 'en',
	messages,
	// 自定义占位符解析
	interpolation: {
		prefix: '%{', // 修改前缀为 %{
		suffix: '}' // 修改后缀为 }
	}
})

// ✅ 提前挂到 window，确保外部 JS 可以访问
window.i18n = i18n

import VueScroll from 'vuescroll'
Vue.use(VueScroll)

Vue.config.productionTip = false

// 创建 Vue 实例
new Vue({
	router,
	i18n,
	render: h => h(App)
}).$mount('#app')