<script>
	export default {
		data() {
			return {
				minlevel: 1,
				maxlevel: 10,
				minsize: '0,02',
				maxsize: '0,20',
				currencyStr: 'R$',
				rotatShowStatus: false,
				timer: null,
				isLoading: true,
				configurationHeight: "640",
				paytableContentHeight: "577",
				ops: {
					vuescroll: {},
					scrollPanel: {
						scrollingX: false, //关闭横向滚动
					},
					rail: {
						keepShow: true,
					},
					bar: {
						hoverStyle: true,
						onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
						background: "hsla(0, 0%, 46%)", //滚动条颜色
						opacity: 0.7, //滚动条透明度
					},
				},
			}
		},
		mounted() {
			this.configurationHeight = window.configurationHeight
			this.paytableContentHeight = window.paytableContentHeight
		},
		methods: {
			open(minlevel, maxlevel, minsize, maxsize) {
				this.isLoading = true
				this.rotatShowStatus = true
				this.minlevel = minlevel || 1
				this.maxlevel = maxlevel || 10
				this.minsize = minsize || '0,02'
				this.maxsize = maxsize || '0,20'
				this.currencyStr = localStorage.getItem("currencyStr") ? localStorage.getItem("currencyStr") : 'R$'
				this.timer = setTimeout(() => {
					this.isLoading = false
				}, 1000)
				this.$nextTick(() => {
					this.$refs.scrollContainer.scrollTo({
						top: 0,
						behavior: 'smooth'
					});
				});
			},

			closeTable() {
				this.rotatShowStatus = false
				window.changeTrophiesIconShow(true);
			}
		}
	}
</script>




