		border: solid #000;
		border-width: 0 2px 2px 0;
		display: inline-block;
		padding: 8px
	}

	.gh-angle-wrapper {
		align-items: center;
		display: flex;
		height: 30px;
		justify-content: center;
		transform: translateY(4px);
		width: 30px
	}

	.angle-right {
		transform: rotate(-45deg);
		-webkit-transform: rotate(-45deg)
	}

	.angle-left {
		transform: rotate(135deg);
		-webkit-transform: rotate(135deg)
	}

	.angle-up {
		transform: rotate(-135deg);
		-webkit-transform: rotate(-135deg)
	}

	.angle-down {
		transform: rotate(45deg);
		-webkit-transform: rotate(45deg)
	}

	.gh-arrow {
		height: 2px;
		position: relative;
		width: 32px
	}

	.gh-arrow-right {
		transform: scale(-1)
	}

	.gh-arrow:after,
	.gh-arrow:before {
		background-color: inherit;
		content: "";
		height: 2px;
		position: absolute;
		width: 22px
	}

	.gh-arrow:after {
