												{{i == 0? $t("WildBanditoDetails.NormalTitle") :`${$t("WildBanditoDetails.FortuneTitle")} ${i}/${roundList.length-1}`}}
											</div>
											<div class="game-free-spin-amount"
												:style="activeIndex<=0 && i == 0 ?`color: rgb(255, 200, 36);` : activeIndex == i ?`color: rgb(255, 200, 36);`: `color: rgb(255, 255, 255);`">
