</template>

<script>
	import Swiper from "swiper"
	import "swiper/swiper.min.css"

	export default {
		props: ["data", "orderNo"],
		data() {
			return {
				currencyStr: 'R$',
				configurationHeight: '640',
				timezone: "(GMT+8:00)",
				showPopup: false,
				showTooltipMod: false,
				showTooltipTop: 0,
				mul: 0,
				meaning: 0,
				activeIndex: -1,
				swiper: null,
				roundList: [{
					id: 0,
					num: 2,
				}],
				detail: [{
					win_info_display: [],
					localeTime: ['-', '-', '-'],
					propsList: [],
					winInfoList: [
						[false, false, false, false],
						[false, false, false, false],
						[false, false, false, false],
						[false, false, false, false],
						[false, false, false, false]
					],
				}],
				ops: {
					vuescroll: {},
					scrollPanel: {
						scrollingX: false, //关闭横向滚动
					},
					rail: {
						keepShow: true,
					},
					bar: {
						hoverStyle: true,
						onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
						background: "hsla(0, 0%, 46%)", //滚动条颜色
						opacity: 0.7, //滚动条透明度
					},
				},
			}
		},
		mounted() {
			this.configurationHeight = window.configurationHeight
			let _this = this
			this.swiper = new Swiper(".swiper-container", {
				loop: false,
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev',
					hiddenClass: 'swiper-button-hidden',
				},
				on: {
					slidePrevTransitionStart: function(obj) {
						_this.activeIndex = obj.activeIndex
					},
					slideNextTransitionStart: function(obj) {
						_this.activeIndex = obj.activeIndex
						if (_this.detail.length === 1) {
							_this.swiper.slidePrev(0)
						}
					},
					transitionStart: function(obj) {
						_this.showTooltipMod = false

						if (_this.detail[obj.activeIndex] && _this.detail[obj.activeIndex].order_no) {
							// window.changeOrderNo(_this.detail[obj.activeIndex].order_no)
						}

						if (_this.activeIndex == 0 && obj.activeIndex == 0) {
							_this.returnList()
						}
					},
				}
			})
			
			this.currencyStr = localStorage.getItem("currencyStr") ? localStorage.getItem("currencyStr") : 'R$'

			this.getdetail()
		},
		beforeDestroy() {
			if (this.swiper) {
				this.swiper.destroy();
				this.swiper = null;
			}
		},
		methods: {
			showWinMul(num, list, winLength) {
				let float = 20
				if (winLength) {
					if (list.length == 4) {
						if (num > 0 && num < 3) {
							float = 8
						}
					} else {
						float = 8
					}
				}
				return float
			},
			upProfitGold(text) {
				let showText = ''
				if (text) {
					showText = text.substr(1)
				}
				return showText
			},
			onScroll(vertical, horizontal, nativeEvent) {
				if (vertical.scrollTop > 0) {

				}
			},
			getWinInfo(win_info_display) {
				if (win_info_display.length) {
					win_info_display.map((detail_son, i) => {
						detail_son.winGold = this.formatArray(detail_son.win)
						detail_son.tWinGold = this.formatArray(detail_son.t_win)
						let icon = ''
						let listLength = detail_son.win_pos_list.length ? detail_son.win_pos_list.length : 0
						let strLebgth = ''

						switch (detail_son.item_type) {
							case 1:
								icon = "history_feature_symbols s_wild";
								break;
							case 2:
								icon = "history_feature_symbols s_scatter";
								break;
							case 3:
								icon = "history_symbols h_skeleton";
								break;
							case 4:
								icon = "history_symbols h_guitar";
								break;
							case 5:
								icon = "history_symbols h_tequila";
								break;
							case 6:
								icon = "history_symbols h_maraca";
								break;
							case 7:
								icon = "history_symbols l_a";
								break;
							case 8:
								icon = "history_symbols l_k";
								break;
							case 9:
								icon = "history_symbols l_q";
								break;
							case 10:
								icon = "history_symbols l_j";
								break;
							case 11:
								icon = "history_symbols l_10";
								break;
							case 19:
								icon = "history_symbols h_skeleton";
								break;
							case 20:
								icon = "history_symbols h_guitar";
								break;
							case 21:
								icon = "history_symbols h_tequila";
								break;
							case 22:
								icon = "history_symbols h_maraca";
								break;
							case 23:
								icon = "history_symbols l_a";
								break;
							case 24:
								icon = "history_symbols l_k";
								break;
							case 25:
