						// 		strLebgth = "doze";
						// 		break;
						// 	case 13:
						// 		strLebgth = "treze";
						// 		break;
						// 	case 14:
						// 		strLebgth = "quatorze";
						// 		break;
						// 	case 15:
						// 		strLebgth = "quinze";
						// 		break;
						// 	case 16:
						// 		strLebgth = "dezesseis";
						// 		break;
						// 	case 17:
						// 		strLebgth = "dezessete";
						// 		break;
						// 	case 18:
						// 		strLebgth = "dezoito";
						// 		break;
						// 	case 19:
						// 		strLebgth = "dezenove";
						// 		break;
						// 	case 20:
						// 		strLebgth = "vinte";
						// }
						if (listLength >= 3 && listLength <= 20) {
							strLebgth = this.$t("WildBanditoDetails.Number" + listLength)
						}
						detail_son.strLebgth = strLebgth
					})
				}
			},
			getPropsList(props_display, obj) {
				if (props_display.length) {
					let propsList = [
						[],
						[],
						[],
						[],
						[]
					]
					let winInfoList = [
						[],
						[],
						[],
						[],
						[]
					]
					let j = -1
					let scatterLen = 0
					obj.typeIcon = false
					props_display.map((detail_son, i) => {
						let icon = ''
						let winShow = false
						if (i % 4 == 0) {
							j++
						}

						if (detail_son >= 19) {
							winShow = true
						}

						winInfoList[j].push(winShow)

						switch (detail_son) {
							case 1:
								icon = "history_feature_symbols s_wild";
								break;
							case 2:
								icon = "history_feature_symbols s_scatter";
								scatterLen++
								break;
							case 3:
								icon = "history_symbols h_skeleton";
								break;
							case 4:
								icon = "history_symbols h_guitar";
								break;
							case 5:
								icon = "history_symbols h_tequila";
								break;
							case 6:
								icon = "history_symbols h_maraca";
								break;
							case 7:
								icon = "history_symbols l_a";
								break;
							case 8:
								icon = "history_symbols l_k";
								break;
							case 9:
								icon = "history_symbols l_q";
								break;
							case 10:
								icon = "history_symbols l_j";
								break;
							case 11:
								icon = "history_symbols l_10";
								break;
							case 19:
								icon = "history_symbols h_skeleton";
								break;
							case 20:
								icon = "history_symbols h_guitar";
								break;
							case 21:
								icon = "history_symbols h_tequila";
								break;
							case 22:
								icon = "history_symbols h_maraca";
								break;
							case 23:
								icon = "history_symbols l_a";
								break;
							case 24:
								icon = "history_symbols l_k";
								break;
							case 25:
								icon = "history_symbols l_q";
								break;
							case 26:
								icon = "history_symbols l_j";
								break;
							case 27:
								icon = "history_symbols l_10";
						}
						propsList[j].push(icon)
					})
					if (obj.type != 1 && scatterLen >= 3) {
						obj.typeIcon = true
					}
					obj.winInfoList = winInfoList
					return propsList
				}
			},
			getdetail() {
				let data = {
					token: this.data.token,
					game_id: this.data.game_id,
					order_no: this.orderNo,
				}
				// window.changeOrderNo(this.orderNo)
				this.$http.post('/api/order/get_detail',
					JSON.stringify(data)).then(response => {
					if (response.data.meta.code == 0) {
						response.data.data.map(item => {
							item.localeTime = this.getLocaleTime(item.bet_time)
							item.props_display = JSON.parse(item.props) ? JSON.parse(item.props) : []
							item.betGold = this.formatArray(item.bet_gold)
							item.profitGold = this.formatArray(item.profit)
							item.endGold = this.formatArray(item.end_gold)
							item.betSize = this.formatArray(item.bet_size)
							// item.frees_display = JSON.parse(item.frees)
							item.win_mul_info_display = item.win_mul_info
							item.win_mul = item.win_mul ? item.win_mul : 1
							item.win_mul_min = (item.win_mul - 1).toString()
							item.win_mul_max = (item.win_mul + 1).toString()
							item.win_mul = item.win_mul.toString()
							item.orderNo = this.getOrderNo(item.order_no)
							item.propsList = this.getPropsList(item.props_display, item)
							item.win_info_display = JSON.parse(item.win_info) ? JSON.parse(item.win_info) :
								[]
							this.getWinInfo(item.win_info_display)
						})
						let scatterId = -1
						let roundList = []
						let roundNum = 1
						let profitNum = 0
						let roundItemList = []
						response.data.data.map((item, i) => {
							if (item.typeIcon) {
								scatterId = i
							}
							if (scatterId != -1 && scatterId < i) {
								item.scatterState = true
								// item.winInfoList = [
								// 	[false, false, false, false],
								// 	[false, false, false, false],
								// 	[true, true, true, true],
								// 	[false, false, false, false],
								// 	[false, false, false, false]
								// ]
							}
							item.roundNum = roundNum
							roundItemList.push(i)
							if (!item.win_info_display.length) {
								let roundObj = {
									id: i - roundNum + 1,
									num: roundNum,
									profit: profitNum,
									profitGold: this.formatArray(profitNum),
									roundItemList: roundItemList,
								}
								roundList.push(roundObj)
								roundNum = 1
								profitNum = 0
								roundItemList = []
							} else {
								profitNum += item.profit
								roundNum++
							}
						})
						response.data.data.map((item, i) => {
							roundList.map((item_son, j) => {
								if (item_son.roundItemList.includes(i)) {
									item.roundTotalNum = item_son.num
									item.pageNum = j
								}
							})
						})
						this.roundList = roundList
						this.detail = response.data.data
						// 数据加载后，刷新轮播
						setTimeout(() => {
							this.swiper.update(true)
							this.activeIndex = 0
						}, 500)
					}
				});
			},
			showTooltip(item, i, obj) {
				this.mul = item.rate
				this.meaning = item.count
				this.showTooltipMod = !this.showTooltipMod
				let x = obj.win_info_display.length - i - 1
				this.showTooltipTop = -48 * x
			},
			toIndex(item, i) {
				this.showPopup = !this.showPopup
				this.swiper.slideTo(item.id)
			},
			changePopup() {
				if (this.roundList.length > 1) {
					this.showPopup = !this.showPopup
				}
			},
			returnList() {
				this.$parent.changeDetails()
			},
			swiperEnd(obj) {
				if (obj.activeIndex == 0 && this.activeIndex == 0) {
					this.returnList()
				}
				this.activeIndex = obj.activeIndex
			},
			getOrderNo(order_no) {
				let orderNo = '-'
				if (order_no) {
					var firstPart = order_no.slice(0, 9); // 获取字符串的前n-1个字符
					var secondPart = order_no.slice(9); // 获取字符串的第n个字符及之后的字符
					orderNo = firstPart + " " + secondPart
				}
				return orderNo
			},
			formatArray(num) {
				let result = [];
				let reverseList = [];
				if (num != null) {
					let numStr = (num / Math.pow(10, 2)).toFixed(2);

					for (let i = numStr.length - 1; i < numStr.length && i >= 0; i--) {
						reverseList.push(numStr[i]);
					}

					for (let i = 0; i < reverseList.length; i++) {
						if (reverseList[i] == '.') {
							result.push(',');
						} else if (((i - (2 + 1)) % 3) === 0 && i > (2 + 1)) {
							if (reverseList[i] !== '-') {
								result.push('.');
							}
							result.push(reverseList[i]);
						} else {
							result.push(reverseList[i]);
						}
					}
				}
				return result.reverse().join('')
			},
			getLocaleTime(time) {
				let timeList = ['-', '-', '-']
				if (time) {
					let date = new Date(time * 1000)
					let yy = date.getFullYear();
					let mm = date.getMonth() + 1;
					mm = mm < 10 ? '0' + mm : mm;
					let dd = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
					let hh = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
					let mf = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
					let ss = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
					timeList[0] = mm + "/" + dd
					timeList[1] = hh + ":" + mf + ":" + ss
					timeList[2] = yy + "/" + mm + "/" + dd + " " + hh + ":" + mf + " " + this.timezone
				}
				return timeList
			},
		}
	}
</script>

